import React, { useState } from 'react';
import { useMediaQuery, useTheme, Backdrop, IconButton, makeStyles } from '@material-ui/core'
import HeaderTabs from './HeaderTabs';
import FlexBox from './FlexBox';
import { ReactComponent as ThreeLineMenuIcon } from '../media/threeLineMenuIcon.svg';

const useStyles = makeStyles((theme) => ({
  backdrop: {
    zIndex: theme.zIndex.drawer + 1,
  },
  headerMenuIconContainer: {
    margin: `0 ${theme.spacing(2)}px`,
  },
  headerMenuIconButton: {
    borderRadius: '25%',
  },
}));

const HeaderMenu = () => {
  const classes = useStyles();
  const theme = useTheme();
  const isLargeWidth = useMediaQuery(theme.breakpoints.up('md'));
  const [open, setOpen] = useState(false);
  if (isLargeWidth) {
    return <HeaderTabs />;
  } else {
    return (
      <>
        <FlexBox className={classes.headerMenuIconContainer}>
          <IconButton onClick={() => setOpen((prevOpen) => !prevOpen)} className={classes.headerMenuIconButton}>
            <ThreeLineMenuIcon />
          </IconButton>
        </FlexBox>
        <Backdrop className={classes.backdrop} open={open} onClick={() => setOpen(false)}>
          <HeaderTabs orientation='vertical' />
        </Backdrop>
      </>
    );
  }
};

export default HeaderMenu;