import React, { useState } from 'react';
import classNames from 'classnames';
import { Tabs, makeStyles } from '@material-ui/core';
import HeaderTabLink, { HeaderTabWithDropdown } from './HeaderTabLink';
import { PageRoutes } from '../models/routes';
import { filledTransparentBackground } from '../utils/styles';
import { useLocation } from 'react-router-dom';

const useStyles = makeStyles((theme) => ({
  tabsContainer: {
    position: 'relative',
  },
  contactTab: {
    bottom: 0,
    right: 0,
    width: 158,
    height: 200,
    display: 'flex',
    position: 'absolute',
    background: '#b2cae4',
    zIndex: -1,
  },
  ...filledTransparentBackground(theme),
}));

type HeaderTabsProps = { orientation?: 'horizontal' | 'vertical' }
const HeaderTabs = ({ orientation }: HeaderTabsProps) => {
  const classes = useStyles();
  const location = useLocation();
  const [tabValue, setTabValue] = useState(({
    [PageRoutes.Services]: 0,
    [PageRoutes.Portfolio]: 1,
    [PageRoutes.About]: 2,
    [PageRoutes.Contact]: 3,
  } as Record<string, number>)[location.pathname] || 0);
  return (
    <div className={classNames(classes.tabsContainer, { [classes.filledTransparentBackground]: orientation === 'vertical' })}>
      <Tabs orientation={orientation} value={tabValue} onChange={(evt, newValue) => {
        setTabValue(newValue);
      }} indicatorColor='primary' centered>
        <HeaderTabLink href={PageRoutes.Services} label='Services' />
        <HeaderTabWithDropdown href={PageRoutes.Portfolio} label='Portfolio' orientation={orientation} subItems={[{ href: '/services/interior', label: 'Staging + Interiors' }, { href: '/services/modeling', label: '3D Modeling' }]} />
        <HeaderTabLink href={PageRoutes.About} label='About' />
        <HeaderTabLink href={PageRoutes.Contact} label='Contact' />
      </Tabs>
      {orientation === 'horizontal' && <div className={classes.contactTab} />}
    </div>
  );
};

export default HeaderTabs;