import React from 'react';
import { Typography as MuiTypography, TypographyProps, makeStyles } from '@material-ui/core';
import classNames from 'classnames';

const useStyles = makeStyles((theme) => ({
  angeliqueBonques: {
    fontFamily: 'AngelicBonques'
  },

}));

const Typography = ({ className, header, children, ...typProps }: TypographyProps & { header?: true }) => {
  const classes = useStyles();
  return (
    <MuiTypography className={classNames(className, { [classes.angeliqueBonques]: header })} {...typProps}>
      {children}
    </MuiTypography>
  );
};

export default Typography;