import React from 'react';
import { TabProps, Tab, makeStyles, Theme, Popper, Grow, ClickAwayListener, MenuList, MenuItem } from '@material-ui/core';
import { Link } from 'react-router-dom';
import FlexBox from './FlexBox';
import { filledTransparentBackground } from '../utils/styles';

const useStyles = makeStyles((theme: Theme) => ({
  tabContainer: {
    padding: `0 ${theme.spacing(2)}px`,
    '& > .MuiTab-wrapper': {
      height: '100%',
    }
  },
  link: {
    color: theme.palette.text.primary,
    textDecoration: 'none',
    height: '100%',
    width: '100%',
    display: 'flex',
    justifyContent: 'center',
    alignItems: 'center',
  },
  popper: {
    zIndex: theme.zIndex.drawer + 2,
    ...filledTransparentBackground(theme).filledTransparentBackground,
  },
}));

const HeaderTabLink = ({ href, label, ...rest }: TabProps & { href: string }) => {
  const classes = useStyles();
  return (
    <Tab className={classes.tabContainer} label={<Link className={classes.link} to={href}>{label}</Link>} {...rest} />
  );
};

type HeaderTabPopperProps = TabProps & { href: string, subItems: { href: string, label: TabProps['label'] }[], orientation?: 'horizontal' | 'vertical' }
export const HeaderTabWithDropdown = ({ href, label, subItems, orientation = 'horizontal', ...rest }: HeaderTabPopperProps) => {
  const classes = useStyles();
  const [open, setOpen] = React.useState(false);
  const anchorRef = React.useRef<HTMLDivElement>(null);

  const handleClose = (event: React.MouseEvent<EventTarget>) => {
    if (anchorRef.current && anchorRef.current.contains(event.target as HTMLElement)) {
      console.log('dont close when clicking a tab');
      return;
    }

    setOpen(false);
  };

  function handleListKeyDown(event: React.KeyboardEvent) {
    if (event.key === 'Tab') {
      event.preventDefault();
      setOpen(false);
    }
  }

  // return focus to the button when we transitioned from !open -> open
  const prevOpen = React.useRef(open);
  React.useEffect(() => {
    if (prevOpen.current === true && open === false) {
      anchorRef.current!.focus();
    }

    prevOpen.current = open;
  }, [open]);
  return (
    <FlexBox>
      <div onMouseEnter={() => setOpen(true)}
        onMouseLeave={() => setOpen(false)}>
        <Tab
          className={classes.tabContainer}
          label={<Link className={classes.link} to={href}>{label}</Link>}
          ref={anchorRef}
          disableFocusRipple
          {...rest}
        />
        <Popper className={classes.popper} open={open} anchorEl={anchorRef.current} role={undefined} transition placement={orientation === 'horizontal' ? 'bottom' : 'right'}>
          {({ TransitionProps, placement }) => (
            <Grow
              {...TransitionProps}
              style={{ transformOrigin: (placement === 'bottom' ? 'center top' : 'center bottom') }}
            >
              <FlexBox>
                <ClickAwayListener onClickAway={handleClose}>
                  <MenuList autoFocusItem={open} id="menu-list-grow" onKeyDown={handleListKeyDown}>
                    {subItems.map(({ href, label }) => (
                      <MenuItem key={href} onClick={handleClose}>{label}</MenuItem>
                    ))}
                  </MenuList>
                </ClickAwayListener>
              </FlexBox>
            </Grow>
          )}
        </Popper>
      </div>
    </FlexBox>
  );
}

export default HeaderTabLink;